import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/effect-cards';
import { EffectCards } from 'swiper/modules';

import "./Reward.scss"
import SectionTitle from "../SectionTitle"
import alisa from "../../assets/alisa.webp"
import liar from "../../assets/liar.webp"

function Reward() {
    return (
        <div className="wrapper">
            <div className="reward">
                <SectionTitle
                    title="Gii Thng"
                    description="Phn thng ginh cho 3 ngi chin thng nm nay! Trt sang tri/phi  ng nh  xem nh tip theo!"
                    titleType="type1"
                />

                <div className="item gold">
                    <div className='goldSwiper'>
                        <Swiper
                            effect={'cards'}
                            grabCursor={true}
                            modules={[EffectCards]}
                            className="mySwiper"
                            initialSlide={4}
                        >
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                            <SwiperSlide></SwiperSlide>
                        </Swiper>
                    </div>
                    <div className='goldContent'>
                        <h1> Gii Nht </h1>
                        <h3>Dengeki Wedding 30th Anniversary Artbook</h3>
                        <p>Cun artbook kch c A4 cho b nh k nim 30 nm ca Dengeki Bunko. Bao gm <strong>81 tm nh vy ci</strong> cho <strong>81 tc phm</strong> trong tng s cc n phm c xut bn ca h trong 30 nm qua.</p>
                        <div><strong> Thn thin nhc nh </strong><br /> Trt sang tri/phi  ng nh  xem thm nh tip theo!</div>
                    </div>
                </div>

                {/* <div className='item goldBonus'>
                    <p>Ngoi ra, ngi ginh Gii Nht bn cnh artbook cn s c chng ti thng nng thm s tin <span> 1.000.000 (1 triu ng) </span> chuyn khon trc tip!</p>
                </div> */}

                <div className='silverBronze'>
                    <div className="item silver">
                        <div className='silverContent'>
                            <h1> Gii Nh </h1>
                            <h3>700.000</h3>
                            <p>Sau khi cn nhc th chng ti quyt nh s ch c Gii Nht c  tht vt tht.
                                Hai gii cn li s l tin mt, Gii Nh s l <strong>700.000</strong> v chng ti a ra hai phng n nhn thng cho qu c gi nh sau.
                                <br /><br /><strong>1.</strong> Nhn tin bng chuyn khon trc tip.
                                <br /><strong>2.</strong> t hng mua sch trc tip trn cc nn tng Fahasa/Shopee/Lazada/Tiki vi tng gi tr n hng khng vt qua s tin ca gii thng.

                            </p>
                        </div>
                    </div>
                    <div className="item bronze">
                        <div className='bronzeContent'>
                            <h1> Gii Ba </h1>
                            <h3>500.000</h3>
                            <p>
                                Tng t Gii Nh th Gii Ba cng s l tin mt vi s tin l <strong>500.000</strong> v cng vi 2 la chn nhn thng nh vy.
                                Tt nhin nu chn phng n mua hng th chng ti khuyn khch l n hng lin quan ti tiu thuyt hoc truyn tranh.
                                <br />Mt mc ch khc ca cuc thi ny vi nhng cu hi trc nghim v phn thng gi tr nh vy chnh l  c v h tr pht trin vn ha c ni chung c bit l light novel ni ring.
                            </p>
                        </div>
                    </div>
                </div>
                <img id="alisa" alt="Alisa" src={alisa} />
                <img id="liar" alt="" src={liar} />
            </div >
        </div >
    );
}

export default Reward;