import { useState, useEffect } from 'react';

import SectionTitle from "../SectionTitle"
import danimaru from "../../assets/danimaru.webp"
import danimaru_stack from "../../assets/danimaru_stack.webp"

import "./Countdown.scss"

function Countdown({ targetDate }) {

    const calculateTimeLeft = () => {
        const difference = new Date(targetDate) - new Date();
        let timeLeft = {};

        if (difference > 0) {
            timeLeft = {
                days: Math.floor(difference / (1000 * 60 * 60 * 24)),
                hours: Math.floor((difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
                minutes: Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60)),
                seconds: Math.floor((difference % (1000 * 60)) / 1000)
            };
        }

        return timeLeft;
    };

    const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());

    useEffect(() => {
        const timer = setTimeout(() => {
            setTimeLeft(calculateTimeLeft());
        }, 1000);

        return () => clearTimeout(timer);
    }, [timeLeft]);

    return (
        <div className="countdown">

            <SectionTitle
                title="Thi Gian"
                description="Chi tit thi gian din ra s kin nm nay!"
                titleType="type1"
            />

            {/* {Object.keys(timeLeft).length !== 0 && <div className='timeCardWrapper'>} */}

            <div className='timeCardWrapper'>

                <img id='stackimage' alt='' src={danimaru_stack} />

                <div className='timeCard'>
                    <div className='timeHeader'>
                        <img alt='' src={danimaru} />
                    </div>

                    <div className='timeInfo'>
                        <h4> Thi gian d kin </h4>
                        <h2>20:00, 02/03/2024</h2>
                        <p>Trn y l thi gian c th m chng ti s m link bi thi  bt u lm bi, lu  link bi thi ny s c cng b trc gi lm bi 5 pht  trn hai trang Facebook  c  cp. Th l bi thi cng s c ghi c th  mc tip theo bn di (ch cn c mc u, nhng ci cn li c th b qua). </p>
                        <p>Cn sau y l thi gian m ngc cho n lc !</p>
                    </div>

                    <div className='timeLeft'>
                        <div><h2>{timeLeft.days < 10 ? "0" + timeLeft.days : timeLeft.days}</h2><span>ngy</span></div>
                        <div><h2>{timeLeft.hours < 10 ? "0" + timeLeft.hours : timeLeft.hours}</h2><span>gi</span></div>
                        <div><h2>{timeLeft.minutes < 10 ? "0" + timeLeft.minutes : timeLeft.minutes}</h2><span>pht</span></div>
                        <div><h2>{timeLeft.seconds < 10 ? "0" + timeLeft.seconds : timeLeft.seconds}</h2><span>giy</span></div>
                    </div>
                </div>

            </div>
        </div>
    );

}

export default Countdown;